/* Responsive Design */

/* iPhone 5 */
@media only screen
and (min-device-width: 320px)
and (max-device-width: 568px) {
  
  body {
    background-position: top !important;
    background-repeat: no-repeat ;
    background-size: 70vh !important;
    line-height: 1. rem;
  }

  .text-white {
    color: black !important;
    padding: 1rem;
  }

  .App-footer {
    height: 5rem;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: black;
  }

  .circle-img {
    height: 300px;
    width: 250px;
    margin-left: 5rem;
    border-width: 5px;
    margin-left: 2.2rem !important;
  }

  .drop-scroll {
    overflow: hidden;
  }

  .about-me {
    margin-right: 0;
  }
  
  /* .position-absolute {
    position: absolute;
  } */

  .scroll {
    overflow-x: scroll;
    /* overflow-y: hidden; */
    /* white-space: nowrap; */
    -webkit-overflow-scrolling: touch;
  }

  .scrolling-wrapper {
    overflow-x: scroll;
    overflow-y: hidden;
    /* white-space: nowrap; */
    -webkit-overflow-scrolling: touch;
  }

  .scrolling-wrapper-2 {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
  }

   .fs-3 {
    font-size: 24px !important;
  }

  .justify {
    -webkit-justify-content: inherit !important;
            justify-content: inherit !important;
  }

  .hover-thumb {
    opacity: 1;
  }

  .jokes-img {
    margin-left: 0 !important;
  }

  .daily {
    margin-right: 1rem;
  }

  .current {
    margin-left: 6rem;
  }

  .hourly-daily-text {
    margin-left: 5.6rem;
  }

  .lh-10 {
    line-height: 3rem;
  }

  .projects-text {
    line-height: 3rem;
  }

  .col {
    padding-left: 1rem !important;
  }
  
  .wh-sp-wr {
    white-space: nowrap;
  }

  .App-footer {
    background-color: black;
  }

  .pb-4, .py-4 {
    padding-bottom: 0 !important;
}

.pr-4, .px-4 {
    padding-right: 0 !important;
}

  /* .pr-4, .px-4 {
    padding-right: 0;
} */

  .footer {
    font-size: 2rem;
  }
}
/* END OF iphone 5 */

